import React, { Component } from "react";
import PoolV2 from "./abis/PoolV2.json";
import Stability from "./abis/Stability.json";
import WETH from "./abis/WETH.json";
import sPROFITV2 from "./abis/sPROFITV2.json";
import getWeb3 from "./getWeb3";

import "./App.css";

class App extends Component {
  state = {
    loaded: false,
    poolBal: 0,
    yourETH: 0,
    amount: 0,
    yourStake: 0,
    poolAddr: "",
    sProfitAddr: "",
    ownerAddr: "",
    Multiplier: 0,
    profitAddr: "",
    reward: "",
  };

  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      this.web3 = await getWeb3();

      // Use web3 to get the user's accounts.
      this.accounts = await this.web3.eth.getAccounts();

      // Get the contract instance.
      this.networkId = await this.web3.eth.getChainId();

      this.pool = new this.web3.eth.Contract(
        PoolV2.abi,
        PoolV2.networks[this.networkId] &&
          PoolV2.networks[this.networkId].address
      );

      this.stability = new this.web3.eth.Contract(
        Stability.abi,
        Stability.networks[this.networkId] &&
          Stability.networks[this.networkId].address
      );

      this.weth = new this.web3.eth.Contract(
        WETH.abi,
        WETH.networks[this.networkId] &&
          WETH.networks[this.networkId].address
      );

      this.sProfit = new this.web3.eth.Contract(
        sPROFITV2.abi,
        sPROFITV2.networks[this.networkId] &&
          sPROFITV2.networks[this.networkId].address
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({
        loaded: true,
      });
      this.getState();
      this.seeReward();
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert("Try Metamask.");
      console.error(error);
    }
  };
  handleInputChange = (event) => {
    const target = event.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const name = target.name;
    this.setState({
      [name]: value,
    });
  };

  getState = async () => {
    console.log(PoolV2.networks[this.networkId].address);
    console.log(typeof(PoolV2.networks[this.networkId].address));
    let result = await this.stability.methods
    .balanceOf(
      PoolV2.networks[this.networkId].address
    ).call();
    console.log(result);
    let result3 = await this.pool.methods.sProfit().call();
    let result4 = await this.weth.methods
      .balanceOf(this.accounts[0])
      .call();
    let result5 = await this.pool.methods.owner().call();
    let result6 = await this.pool.methods.getMultiplier(0).call();
    let result7 = await this.sProfit.methods
      .tokenInfo(0)
      .call({ from: this.accounts[0] });
    let result9 = await this.sProfit.methods.balanceOf(this.accounts[0]).call();
    this.setState({
      poolBal: this.web3.utils.fromWei(result.toString(), "ether"),
      yourETH: this.web3.utils.fromWei(result4.toString(), "ether"),
      sProfitAddr: result3,
      poolAddr: "0xFAAeD4aA793246B116DD49775640090D692d93ce",
      ownerAddr: result5,
      Multiplier: result6,
      profitAddr: result7,
      yourStake: this.web3.utils.fromWei(result9.toString(), "ether"),
    });
  };

  seeReward = async () => {
    let result = await this.pool.methods
      .pendingWETH(0, this.accounts[0])
      .call();
    console.log(result);
    this.setState({
      reward: this.web3.utils.fromWei(result, "ether") + " ETH.",
    });
  };

  collectReward = async () => {
    try {
      let result = await this.pool.methods
        .harvestEarnings(0, 0)
        .send({ from: this.accounts[0] });
      console.log(result);
      //let upDateAmount = this.web3.utils.fromWei(result.events.RewardChanged.returnValues._totalTrident);
      alert("Harvest Earnings Successful!");
    } catch (error) {
      alert("You don't have any rewards yet.");
    }
  };

  handleDeposit = async () => {
    const { amount } = this.state;
    await this.stability.methods
      .approve(this.state.poolAddr, this.web3.utils.toWei(amount.toString(), "ether"))
      .send({ from: this.accounts[0] });
    let result = await this.pool.methods
      .stake(0, this.web3.utils.toWei(amount.toString(), "ether"))
      .send({ from: this.accounts[0] });
    console.log(result);
    // this.setState({
    // poolBal: this.web3.utils.fromWei(result.events.Deposit.returnValues._total, 'ether'),
    // juice: this.web3.utils.fromWei(result.events.Deposit.returnValues._totalTrident, 'ether')
    // })
  };

  handleWithdraw = async () => {
    const { amount } = this.state;
    let result = await this.pool.methods
      .unstake(0, this.web3.utils.toWei(amount, "ether"))
      .send({ from: this.accounts[0] });
    console.log(result);
    // this.setState({
    // poolBal: this.web3.utils.fromWei(result.events.Deposit.returnValues._total, 'ether'),
    // yourETH: this.state.yourETH -= this.web3.utils.fromWei(result.events.Deposit.returnValues._amount, 'ether')
    // })
  };

  english = () => {
    this.setState({});
  };

  render() {
    if (!this.state.loaded) {
      return (
        <div className="App">
          <div className="Main">
            <h1 className="shadowText">Stability Staking Pool</h1>
            <p>Stake PROFIT Tokens to Earn.</p>
            <h2>Total Staked: 0</h2>
            <h3>Your Stake: {this.state.yourStake}</h3>
            <h3>Multiplier: {this.state.Multiplier}x</h3>
            <h3>Earnings: {this.state.reward}</h3>
            <button type="button" onClick={this.seeReward}>
              Calculate Earnings
            </button>
            <button type="button" onClick={this.collectReward}>
              Harvest Earnings
            </button>
            <h3>Balance: {this.state.yourETH} ETH</h3>
            <input
              type="text"
              placeholder="Stake Amount"
              name="amount"
              value={this.state.amount}
              onChange={this.handleInputChange}
            />
            <button type="button" onClick={this.handleDeposit}>
              Stake
            </button>
            <button type="button" onClick={this.handleWithdraw}>
              Unstake
            </button>
          </div>
          <div className="InfoContainer">
            Stability Token (PROFIT):
            <div className="InfoBox">
              <p>0xf81FCd61b18BAb470418161B6cFaF95a3796762b</p>
            </div>
            Stability PoS Token (sPROFIT):
            <div className="InfoBox">
              <p>0x85db54A463433EccA28BA4047cb95b653F623f92</p>
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <div className="App">
          <div id="Main"></div>
          <div className=".container">
            <div className="Main">
              <h1 className="shadowText">Stability Staking Pool</h1>
              <p>Stake PROFIT Tokens to Earn. </p>

              <p>Multiplier: {this.state.Multiplier}x</p>
              <h2 className="shadowTextStrong">
                Total Staked: {this.state.poolBal}
                <p className="shadowTextStrong">
                  Your Stake: {this.state.yourStake}
                </p>
              </h2>
              <p>Balance: {this.state.yourETH} ETH</p>

              <h3 className="shadowTextStrong">
                {" "}
                Earnings: {this.state.reward}{" "}
              </h3>
              <div className="row">
                <button type="button" onClick={this.seeReward}>
                  Calculate Earnings
                </button>
                <button type="button" onClick={this.collectReward}>
                  Harvest Earnings
                </button>
              </div>
              <div className="row">
                <input
                  type="text"
                  placeholder="Stake Amount"
                  name="amount"
                  value={this.state.amount}
                  onChange={this.handleInputChange}
                />
                <button type="button" onClick={this.handleDeposit}>
                  Stake
                </button>
                <button type="button" onClick={this.handleWithdraw}>
                  Unstake
                </button>
              </div>
            </div>
          </div>
          <div className="InfoContainer">
            Stability Token (PROFIT)
            <div className="InfoBox">
              <p>{this.state.profitAddr}</p>
            </div>
            Stability PoS Token (sPROFIT):
            <div className="InfoBox">
              <p>{this.state.sProfitAddr}</p>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default App;
